.main{
  background-size: cover;
  background-position: center;
  &__inner{
    padding: 17.5em 0 1.25em;
    position: relative;
    z-index: 2;
  }
  &__title{
    color: #fff;
    text-align: center;
    max-width: 16em;
    margin: 0 auto;
  }
  &__bottom{
    margin-top: 11em;
    display: flex;
    align-items: center;
  }
}

.main-search{
  background-color: #d6dce4;
  border-radius: .5em;
  padding: 1.25em;
  flex-grow: 1;
  display: flex;
  max-width: 63.875em;
  &__date,
  &__select{
    margin-right: 1.5em;
    flex-grow: 1;
  }
  &__select{
    max-width: 17.5em;
  }
  &__date{
    max-width: 13.75em;
  }
  &__btn{
    flex-grow: 1;
    max-width: 8.75em;
  }
}

.main-gift{
  display: flex;
  align-items: center;
  margin-left: 2.125em;
  &__img{
    width: 3.125em;
    flex-shrink: 0;
    margin-right: 1em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__text{
    color: #fff;
  }
}

.tours{
  padding: 3.125em 0 6.25em;
  &__top{
    @include grid100gap(1.25em);
  }
}

.slider{
  position: relative;
  &__wrap{
    overflow: hidden;
  }
  .swiper {
    &-container {
      width: calc(100% + 2.25em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      padding-right: 2.25em;
      height: auto;
    }
    &-button{
      margin: 0;
      top: 50%;
      transform: translateY(-50%);
      &-prev{
        left: -1.375em;
      }
      &-next{
        right: -1.375em;
      }
    }
  }
}

.tours-slider{
  margin-top: 2.5em;
  .swiper{
    &-slide{
      width: 50%;
    }
    &-button{
      transform: unset;
      top: 11.875em;
    }
  }
}

.tour-card{
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  .favourite-btn{
    position: absolute;
    top: 1em;
    right: 1em;
    z-index: 2;
  }
  &__img{
    display: block;
    border-radius: .5em;
    @include imgCover(65.3%);
  }
  &__content{
    margin-top: 1.25em;
    margin-bottom: 1.125em;
  }
  &__date{
    margin-bottom: .4375em;
  }
  &__desc{
    margin-top: .75em;
  }
  &__bottom{
    margin-top: auto;
    padding-top: 1.125em;
    border-top: 1px solid #cddbe3;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__info{
    @include grid100gap(.25em);
  }
  &__type{
    .text16{
      line-height: 138%;
    }
  }
  &__price{
    min-width: 8.75em;
    height: 2.75em;
    margin-left: 1em;
    padding: 0 .4375em;
    @include center;
    background-color: #a0c5db;
    border-radius: .5em;
    white-space: nowrap;
    .text13{
      margin-right: .67em;
    }
  }
  &._small{
    .tour-card{
      &__img{
        padding-bottom: 100%;
      }
    }
  }
}

.our-counts{
  padding: 6em 0 7em;
  background-size: cover;
  background-position: center;
  color: #fff;
  &__items{
    margin-top: 2em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 2.125em));
    gap: 4em 4.25em;
    max-width: 28.5em;
  }
}

.count-item{
  &__num{
    font-size: 3.25em;
    color: #7accf2;
    font-weight: 600;
  }
  &__text{
    margin-top: .625em;
  }
}

.our-offers{
  padding: 7.1875em 0 18.25em;
  background-color: #004776;
  position: relative;
  color: #fff;
  &__mask{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 12.25em;
    overflow: hidden;
    svg{
      height: auto;
      width: 100%;
    }
  }
}

.offers-slider{
  margin-top: 2.275em;
  .swiper{
    &-slide{
      width: 25%;
    }
  }
}

.offer-card{
  position: relative;
  &__img{
    border-radius: .5em;
    @include imgCover(150%);
    &:before{
      background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0.7));
      z-index: 2;
    }
  }
  &__text{
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 1.5em .5em 1.5em 1.5em;
    z-index: 2;
  }
}

.popular-tours{
  margin-top: -4.75em;
  position: relative;
  z-index: 2;
  padding: 1.75em 0 6.125em;
  background-color: #fff;
  &__top{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }
  &__text{
    max-width: 43em;
    @include grid100gap(1.75em);
  }
  &__items{
    margin-top: 3.75em;
    row-gap: 3.75em;
  }
}

.our-offers2{
  padding: 8.375em 0 3.75em;
  clip-path: polygon( 5.556% 8.66%,0% 8.66%,0% 24.899%,-0.069% 24.899%,-0.069% 100%,99.931% 100%,99.931% 34.641%,100% 34.641%,100% 8.66%,94.444% 6.495%,94.444% 6.495%,92.617% 5.787%,90.489% 4.988%,88.094% 4.137%,85.467% 3.274%,82.639% 2.436%,79.644% 1.663%,76.517% 0.994%,73.289% 0.468%,69.994% 0.123%,66.667% 0%,66.667% 0%,64.444% 0.056%,62.222% 0.216%,60% 0.468%,57.778% 0.801%,55.556% 1.203%,53.333% 1.663%,51.111% 2.169%,48.889% 2.71%,46.667% 3.274%,44.444% 3.849%,44.444% 3.849%,43.333% 4.137%,42.222% 4.424%,41.111% 4.709%,40% 4.988%,38.889% 5.262%,37.778% 5.529%,36.667% 5.787%,35.556% 6.035%,34.444% 6.272%,33.333% 6.495%,33.333% 6.495%,30.006% 7.082%,26.711% 7.552%,23.483% 7.918%,20.356% 8.193%,17.361% 8.39%,14.533% 8.522%,11.906% 8.602%,9.511% 8.643%,7.383% 8.658%,5.556% 8.66% );
  .our-offers__mask{
    display: none;
  }
}

.reviews{
  padding: 6.25em 0 3.125em;
}

.reviews-slider{
  margin-top: 2em;
  @media screen and (min-width: 993px){
    .swiper {
      &-container {
        width: calc(100% + 2.875em);
      }
      &-slide {
        padding-right: 2.2875em;
        width: calc(100% / 3);
      }
    }
  }
}

.review-card{
  display: block;
  height: 100%;
  padding: 2em 1.875em 3.625em 2.5em;
  background-color: #f9f9f9;
  border-radius: .5em;
  &:hover{
    color: $black;
  }
  &__text{
    font-size: 1.125em;
    line-height: 133%;
    p{
      line-height: inherit;
      @include lastM(0 0 1em);
    }
  }
  &__info{
    margin-top: 1.25em;
    @include grid100gap(.3125em);
  }
}

.feedback{
  padding: 3.125em 0 0;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 36.625em) 26.625em;
    justify-content: space-between;
    gap: 1.875em;
    max-width: 74.5em;
  }
  &__title{
    max-width: 12em;
  }
  &__desc{
    margin-top: 1.75em;
    max-width: 37.5em;
  }
  &__form{
    &-fields{
      margin-bottom: 1em;
      @include grid100gap(1.25em);
    }
    &-btn{
      margin-top: 2em;
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    &__inner{
      padding: 10.5em 0 3.125em;
    }
    &__title{
      letter-spacing: -.04em;
    }
    &__bottom{
      display: block;
      margin-top: 6.25em;
    }
  }

  .main-search{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
    &__date,
    &__select{
      max-width: unset;
      margin-right: 0;
      flex-grow: 1;
    }
    &__btn{
      max-width: unset;
    }
  }

  .main-gift{
    margin: 1.875em 0 0 2em;
  }

  .tours{
    padding: 2.5em 0 3.75em;
    overflow: hidden;
    &__top{
      gap: 1em;
    }
  }

  .slider{
    .slider__wrap{
      overflow: unset;
    }
    .swiper{
      &-container{
        width: 100%;
      }
      &-slide{
        padding-right: 1.25em;
      }
      &-button{
        display: none;
      }
    }
  }

  .tours-slider{
    margin-top: 1.875em;
    .swiper{
      &-slide{
        width: 20em;
      }
    }
  }

  .tour-card{
    .favourite-btn{
      top: .75em;
      right: .75em;
    }
    .text24-tablet{
      line-height: 125%;
    }
    &__content{
      margin-top: 1em;
    }
    &__bottom{
      flex-direction: column;
      align-items: flex-start;
      padding-top: .9375em;
    }
    &__price{
      margin: 1.25em 0 0;
    }
  }

  .our-counts{
    padding: 3.125em 0 3.75em;
    &__items{
      grid-template-columns: repeat(2,calc(50% - .9375em));
      gap: 3.25em 1.875em;
      max-width: 27.5em;
    }
  }

  .our-offers{
    padding-top: 2.5em;
    padding-bottom: 8em;
    overflow: hidden;
    &__mask{
      height: 5em;
    }
  }

  .offers-slider{
    margin-top: 2em;
    .swiper{
      &-slide{
        width: 17.5em;
      }
    }
  }

  .popular-tours{
    margin-top: -.3125em;
    padding: .5em 0 2.25em;
    &__top{
      flex-direction: column;
      align-items: flex-start;
    }
    &__text{
      gap: 1em;
    }
    &__link{
      order: -1;
      margin: 0 0 1em;
    }
    &__items{
      margin-top: 3.125em;
      row-gap: 2.25em;
    }
  }

  .our-offers2{
    padding: 5em 0 3.125em;
    clip-path: polygon( 5.556% 8.66%,0% 8.66%,0% 24.899%,-0.069% 24.899%,-0.069% 100%,99.931% 100%,99.931% 34.641%,100% 34.641%,100% 8.66%,94.444% 6.495%,94.444% 6.495%,92.617% 5.787%,90.489% 4.988%,88.094% 4.137%,85.467% 3.274%,82.639% 2.436%,79.644% 1.663%,76.517% 0.994%,73.289% 0.468%,69.994% 0.123%,66.667% 0%,66.667% 0%,64.444% 0.056%,62.222% 0.216%,60% 0.468%,57.778% 0.801%,55.556% 1.203%,53.333% 1.663%,51.111% 2.169%,48.889% 2.71%,46.667% 3.274%,44.444% 3.849%,44.444% 3.849%,43.333% 4.137%,42.222% 4.424%,41.111% 4.709%,40% 4.988%,38.889% 5.262%,37.778% 5.529%,36.667% 5.787%,35.556% 6.035%,34.444% 6.272%,33.333% 6.495%,33.333% 6.495%,30.006% 7.082%,26.711% 7.552%,23.483% 7.918%,20.356% 8.193%,17.361% 8.39%,14.533% 8.522%,11.906% 8.602%,9.511% 8.643%,7.383% 8.658%,5.556% 8.66% );
    .our-offers__mask{
      display: none;
    }
  }

  .reviews{
    padding: 2.5em 0 1.4375em;
    overflow: hidden;
  }

  .reviews-slider{
    margin-top: 1.25em;
    .swiper{
      &-slide{
        width: 20em;
      }
    }
  }

  .review-card{
    padding: 2em 1.25em;
    &__text{
      font-size: 1em;
      line-height: 138%;
    }
    &__info{
      margin-top: .75em;
      gap: .4375em;
    }
  }

  .feedback{
    padding: 1.4375em 0 1em;
    &__inner{
      grid-template-columns: 100%;
    }
    &__desc{
      margin-top: 1em;
    }
    &__form{
      &-btn{
        margin-top: 1.25em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .main-search{
    padding: 1.875em 1.25em;
    grid-template-columns: 100%;
  }

  .our-offers{
    padding-bottom: 5.875em;
    &__mask{
      height: 3em;
      svg{
        height: 100%;
        width: auto;
      }
    }
  }

  .popular-tours{
    &__items{
      row-gap: 2.5em;
    }
  }

  .our-offers2{
    padding: 0;
    background-color: transparent;
    clip-path: unset;
    &__inner{
      background-color: #004776;
      padding: 1em 0 3.125em;
    }
    .our-offers__mask{
      position: static;
      display: block;
      height: auto;
      img{
        margin-left: -5%;
        width: 110%;
        display: block;
      }
    }
  }
}
