.header{
  padding: 1.25em 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  color: #fff;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  &__right{
    padding-top: .875em;
    display: flex;
    align-items: center;
    align-self: flex-start;
  }
  &__logo{
    width: 10em;
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
}

.m-nav{
  display: flex;
  align-items: center;
  &__link{
    @include center;
    height: 2em;
    padding: 0 .875em;
    border-radius: .5em;
    @include lastM(0 .875em 0 0);
    span{
      font-size: 1.125em;
      line-height: 120%;
    }
    &._active{
      background-color: $secondary;
    }
  }
}

.header-action{
  display: flex;
  align-items: center;
  margin-left: 1.25em;
  &__cart,
  &__favourite{
    flex-shrink: 0;
    @include center;
    position: relative;
    img{
      width: 100%;
      display: block;
    }
  }
  &__favourite{
    width: 1.375em;
  }
  &__cart{
    margin-left: 2em;
    width: 1.9375em;
  }
  &__lang{
    margin-left: 2em;
    text-transform: uppercase;
    span{
      font-size: 1em;
    }
  }
}

._pin{
  position: absolute;
  background-color: $secondary;
  min-width: 1em;
  height: 1em;
  padding: 0 .125em;
  border-radius: 50%;
  @include center;
  color: #fff;
  left: 50%;
  transform: translateX(-40%);
  top: -.1875em;
  z-index: 2;
  span{
    font-size: .625em;
  }
}

.m-menu{
  &__close{
    display: none;
  }
}

.burger{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    &__inner{
      align-items: center;
    }
    &__right{
      align-self: unset;
      padding: 0;
    }
    &__logo{
      width: 7.5em;
    }
    &._open{
      .m-menu{
        transform: translateX(0);
      }
    }
  }

  .m-menu{
    position: fixed;
    transition: all .4s;
    transform: translateX(-120%);
    width: 100%;
    height: $vh;
    top: 0;
    left: 0;
    background-color: #004776;
    z-index: 20;
    padding: 1.5em 1.25em;
    &__close{
      width: 1.5em;
      @include center;
      margin-left: auto;
      margin-bottom: 1.75em;
      img{
        width: 100%;
        display: block;
      }
    }
  }

  .m-nav{
    flex-direction: column;
    align-items: flex-start;
    &__link{
      padding: 0;
      height: unset;
      @include lastM(0 0 2em);
      &._active{
        background-color: transparent;
      }
      span{
        font-size: 1.25em;
        line-height: 137%;
      }
    }
  }

  .header-action{
    &__lang{
      display: none;
    }
  }

  .burger{
    width: 1.625em;
    height: .75em;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-left: 1.75em;
    span{
      width: 100%;
      height: 2px;
      background-color: #fff;
      border-radius: .5em;
    }
  }
}
