.fg {
  position: relative;
  margin: 0;
  input,
  select{
    font-size: 1em;
    height: 2.75em;
    padding: 0 1em;
    border-radius: .25em;
  }
  select{
    padding-right: 2em;
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3e%3cpath fill='none' stroke='%23689dc5' stroke-linecap='round' stroke-linejoin='round' stroke-width='2' d='m2 5 6 6 6-6'/%3e%3c/svg%3e");
    background-size: .875em auto;
  }
  .date-input{
    background-image: url(../img/calendar.svg);
    background-size: 1.5em auto;
    background-repeat: no-repeat;
    background-position: right 1em center;
  }
  &--gray{
    input{
      background-color: #f7f7f7;
      border-color: #f7f7f7;
    }
  }
}

.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: block;
    span{
      font-size: 1em;
      line-height: 137%;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: .0625em;
      transition: all .100s;
      background-color: #fff;
    }
    a{
      position: relative;
      z-index: 2;
    }
  }
  &._gray{
    label{
      color: #6B6E72;
    }
  }
  &._error{
    input:not(:checked){
      &~label:before{
        border-color: #FF1515;
      }
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-color: #127fc9;
        border-color: #127fc9;
        background-size: .6875em auto;
      }
    }
  }
  label {
    padding-left: 2.25em;
    min-height: 1.25em;
    &:before {
      border: 1px solid #f7f7f7;
      width: 1.25em;
      height: 1.25em;
      border-radius: .25em;
      @include bgImgSize(0);
      background-image: url(../img/check.svg);
      transition: 0s;
    }
  }
}
