.tour{
  background-size: cover;
  background-position: center;
  color: #fff;
  &__inner{
    min-height: 41.25em;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    padding: 6em 0 3.75em;
    position: relative;
    z-index: 2;
  }
  &__text{
    @include grid100gap(1.25em);
  }
  &__date{
    color: #7accf2;
  }
  &__nav{
    margin-left: 1em;
    flex-shrink: 0;
    padding-bottom: 3em;
  }
}

.tour-nav{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  background-color: #fff;
  transition: all .4s;
  box-shadow: 0 13px 24px 0 rgba(0, 0, 0, 0.1);
  &:not(._fixed){
    transform: translateY(-120%);
  }
  &__inner{
    padding: 1.125em 0;
    display: flex;
    align-items: center;
  }
  &__link{
    font-weight: 600;
    @include lastM(0 3.75em 0 0);
    span{
      font-size: 1.125em;
      line-height: 133%;
    }
  }
}

.tour-detail{
  padding: 2.5em 0;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 25.625em - 2.25em) 25.625em;
    gap: 2.25em;
  }
}

.tour-props{
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.125em));
    gap: 1.5em 2.25em;
  }
  &__item{
    background-color: #f7f7f7;
    border-radius: .5em;
    padding: 1.1875em 1.375em;
  }
  &__text{
    margin-top: .5em;
    .text18{
      line-height: 133%;
    }
  }
  &__btn{
    margin-top: 1em;
  }
  &:not(._show-all){
    .tour-props__item:nth-child(4)~.tour-props__item{
      display: none;
    }
  }
  &._show-all{
    .tour-info__btn{
      display: none;
    }
  }
}

.more-btn{
  @include center;
  height: 2em;
  background-color: #f7f7f7;
  border-radius: .5em;
  width: 100%;
  &:hover{
    background-color: darken(#f7f7f7,10%);
  }
  img{
    width: .875em;
    display: block;
  }
}

.tour-desc{
  margin-top: 2.5em;
  &:not(._show-all){
    .text *:nth-child(3)~*{
      display: none;
    }
  }
  &__btn{
    margin-top: 1.5em;
  }
  &._show-all{
    .tour-desc__btn{
      display: none;
    }
  }
}

.tour-program{
  margin-top: 3.75em;
  &__items{
    margin-top: 2em;
  }
}

.program-card{
  border-top: solid 1px #cddbe3;
  &:last-child{
    border-bottom: solid 1px #cddbe3;
  }
  &__show{
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    padding: 1.0625em 0;
  }
  &__btn{
    padding: 0;
    width: 2.75em;
    height: 2.75em;
    border-radius: 50%;
    @include center;
    flex-shrink: 0;
    margin-left: 1em;
    img{
      width: .875em;
    }
  }
  &__hidden{
    padding-top: .1875em;
    padding-bottom: 1.875em;
  }
  &__text{
    margin-top: 2em;
    p{
      @include lastM(0 0 .75em);
    }
  }
}

.gallery{
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 1em / 3));
  gap: .5em;
}

.gallery-item{
  display: block;
  position: relative;
  &__img{
    display: block;
    border-radius: .5em;
    @include imgCover(77.2%);
  }
  &__count{
    position: absolute;
    border-radius: .5em;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    @include center;
    z-index: 2;
    color: #fff;
    background-color: rgba(0, 71, 118, 0.6);
    font-weight: 600;
  }
}

.visit-places{
  margin-top: 2.5em;
  padding: 1.5em 1.25em 2.25em;
  background-color: #f7f7f7;
  border-radius: .5em;
}


@media screen and (max-width: 992px){
  .tour{
    &__inner{
      min-height: $vh;
      padding: 5em 0 1.75em;
    }
    &__text{
      gap: 1em;
      padding-bottom: .3125em;
    }
    &__nav{
      padding-bottom: 0;
      margin-left: .3125em;
    }
  }

  .tour-nav{
    .container{
      padding: 0;
    }
    &__inner{
      padding: 1.125em;
      overflow-x: auto;
    }
    &__link{
      @include lastM(0 1.5em 0 0);
      span{
        font-size: 1em;
        line-height: 150%;
      }
    }
  }

  .tour-detail{
    padding: 2em 0;
    &__inner{
      grid-template-columns: 100%;
      gap: 0;
    }
  }

  .tour-props{
    &__items{
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
    }
    &__btn{
      margin-top: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .tour-props{
    &__items{
      grid-template-columns: 100%;
    }
  }
}
