.text48{
  font-size: 3em;
  line-height: 110%;
}

.text46{
  font-size: 2.875em;
  line-height: 110%;
}

.text26{
  font-size: 1.625em;
  line-height: 123%;
}

.text24{
  font-size: 1.5em;
  line-height: 118%;
}

.text22{
  font-size: 1.375em;
  line-height: 110%;
}

.text20{
  font-size: 1.25em;
  line-height: 135%;
}

.text18{
  font-size: 1.125em;
  line-height: 140%;
}

.text16{
  font-size: 1em;
  line-height: 150%;
}

.text13{
  font-size: .8125em;
  line-height: 150%;
}

.bold-text{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

.blue-text{
  color: $secondary;
}

.red-text{
  color: $red;
}

.text{
  p{
    font-size: 1.125em;
    line-height: 133%;
    @include lastM(0 0 1em);
  }
  hr{
    height: 1.25em;
    opacity: 0;
    display: block;
    margin: 0;
  }
}


@media screen and (max-width: 992px){
  .text46{
    font-size: 2.25em;
    line-height: 117%;
  }

  .text24-tablet{
    font-size: 1.5em;
    line-height: 170%;
  }

  .text20-tablet{
    font-size: 1.25em;
  }

  .text18-tablet{
    font-size: 1.125em;
  }

  .text16-tablet{
    font-size: 1em;
  }

  .text {
    p {
      @include lastM(0 0 .33em);
    }
  }
}
