@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    height: 2.75em;
    padding: 0 2.25em;
    border-radius: .5em;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1em;
        line-height: 120%;
    }
}

.m-btn-yellow{
    background-color: $primary;
    color: #fff;
    &:hover{
        background-color: darken($primary,10%);
        color: #fff;
    }
}

.m-btn-blue{
    background-color: $secondary;
    color: #fff;
    &:hover{
        background-color: darken($secondary,10%);
        color: #fff;
    }
}

.arrow-btn{
    display: flex;
    align-items: center;
    font-weight: 600;
    color: $secondary;
    &:hover{
        color: $primary;
        path{
            stroke: $primary;
        }
    }
    span{
        font-size: 1.125em;
        line-height: 133%;
    }
    svg{
        width: .825em;
        height: auto;
        margin-left: .5em;
    }
}
