@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_grid.scss";
.m-backdrop{
  position: relative;
  &:before{
    content: '';
    position: absolute;
    background-color: rgba(0, 0, 0, 0.45);
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: inherit;
  }
}

.favourite-btn{
  width: 2.75em;
  height: 2.75em;
  border-radius: 50%;
  background-color: #fff;
  @include center;
  &:hover{
    path{
      fill: rgba($red,.7);
    }
  }
  svg{
    width: 1.5em;
    height: auto;
  }
  &._active{
    path{
      fill: $red;
    }
  }
}

.swiper-button{
  background-color: $secondary;
  width: 2.75em;
  height: 2.75em;
  @include center;
  border-radius: 50%;
  &-disabled{
    background-color: #A0C5DB !important;
    opacity: 1 !important;
  }
  img{
    width: 1em;
  }
  &:hover{
    background-color: darken($secondary,10%);
  }
}


@media screen and (max-width: 992px){
  .favourite-btn{
    width: 2em;
    height: 2em;
    svg{
      width: 1.125em;
    }
  }
}
