.footer{
  position: relative;
  color: #fff;
  &__bg{
    width: 100%;
    display: block;
  }
  &__inner{
    position: absolute;
    width: 100%;
    left: 0;
    bottom: 0;
    z-index: 2;
    padding-bottom: 1.5em;
  }
  &__top{
    display: flex;
  }
  &__logo{
    display: block;
    width: 11em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__socials{
    max-width: 11.25em;
    margin-top: 1.5em;
    &-whatsapp{
      color: #22d366;
    }
    &-tg{
      color: #2da6dd;
    }
    &-vk{
      color: #0077ff;
    }
  }
  &__bottom{
    display: flex;
    justify-content: space-between;
    margin-top: 7em;
  }
  &__num{
    max-width: 15.5em;
  }
  &__by{
    a{
      color: #a0c5db;
      &:hover{
        color: $primary;
      }
    }
  }
}

.footer-contacts{
  margin-left: 10.1875em;
  &__phones{
    margin-bottom: 1.3125em;
    @include grid100gap(.3125em);
  }
}


.footer-nav{
  margin-left: auto;
  margin-right: 6.25em;
  max-width: 20em;
  column-count: 2;
  column-gap: .5em;
  &__link{
    display: inline-flex;
    width: 100%;
    font-weight: 600;
    @include lastM(0 0 1.375em);
    span{
      font-size: 1.125em;
      line-height: 133%;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 12em 0 2.1875em;
    overflow: hidden;
    &__bg{
      position: absolute;
      height: 100%;
      width: auto;
      top: 0;
      left: 0;
    }
    &__inner{
      position: relative;
      z-index: 2;
      padding: 0;
    }
    &__top{
      flex-direction: column;
      align-items: flex-start;
    }
    &__bottom{
      margin-top: 2.5em;
      flex-wrap: wrap;
      justify-content: unset;
      width: calc(100% + 1.25em);
    }
    &__num{
      width: 100%;
      max-width: unset;
    }
    &__link{
      margin: 1.25em 1.125em 0 0;
    }
    &__c{
      order: 2;
      width: 100%;
      margin-top: 1.25em;
    }
    &__by{
      order: 3;
      margin-top: 1.25em;
      width: 100%;
    }
  }

  .footer-contacts{
    margin: 2.5em 0 0;
  }

  .footer-nav{
    margin: 2.5em 0 0 .5em;
  }
}
